import { FirebaseConfig } from "./keys";
import { auth, initializeApp, database } from 'firebase/app';
import 'firebase/auth';        // for authentication
import 'firebase/database';    // for realtime database
// import 'firebase/firestore';   // for cloud firestore
// import 'firebase/messaging';   // for cloud messaging
// import 'firebase/functions';   // for cloud functions
// import 'firebase/storage';     // for storage

initializeApp(FirebaseConfig);

const databaseRef = database().ref();

const firebaseAuth = auth();

const getEncryptMsgRef = (username) => {
    return databaseRef.child(`encryptMsg/${username}`)
};

const chatMsgRef = databaseRef.child("chatMsg");
const appVersionRef = databaseRef.child("appVersion");

export {
    firebaseAuth,
    chatMsgRef,
    appVersionRef,
    getEncryptMsgRef
}