import _ from "lodash";
import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { withRouter } from "react-router-dom";
import { firebaseAuth } from "firebaseUtil/firebase";
import { reduxDispatch } from "redux/store";
import * as loginTypes from "redux/module/loginModule/action";
import * as paths from "routes/constUrl";
import { appVersionRef } from "firebaseUtil/firebase";
import doSaveAppVersion from "firebaseUtil/appVersion";
import productionVersion from "deployment/appversion";

const VerifyLogin = ({ history }) => {

    const { ispassdoorman } = useSelector(state => state.login);
    const { user } = useSelector(state => state.login);
    const { appVersion } = useSelector(state => state.login);
    const [localAppVersion, setLocalAppVersion] = useState(undefined);
    const newestAppVersion = productionVersion;

    useEffect(() => {
        if (!ispassdoorman) {
            firebaseAuth.signOut()
                .then(() => {
                    //success
                })
                .catch(() => {
                    //error
                });

            history.push(paths.DoormanPageUrl);
        }
    }, [])

    //檢查當前使用版本 (local) 跟最新版本 (firebase) 是否有差異，若有差異則強迫使用者重新載入
    useEffect(() => {
        if (localAppVersion === undefined && appVersion) {
            setLocalAppVersion(appVersion);
        }

        if (localAppVersion !== undefined && localAppVersion !== appVersion) {

            // 開發測試階段跳過驗證
            if (process.env.NODE_ENV === "development") {
                console.log(`localAppVersion: ${localAppVersion}`)
                console.log(`appVersion: ${appVersion}`)
            }

            window.location.href = "about:blank";
        }

    }, [localAppVersion, appVersion])

    //讀取最新版本 (firebase)
    useEffect(() => {
        const maxMessageNumber = 5;

        if (appVersionRef) {
            appVersionRef.limitToLast(maxMessageNumber).on("value", snapshot => {
                const data = snapshot.val();
                if (data !== null && data !== undefined) {
                    // 最多顯示 5 則版本資訊
                    const sliceData = _.slice(_.values(data), -maxMessageNumber);

                    // 最新一則 Po 文的使用者
                    const currentVersion = _.slice(_.values(sliceData), -1)[0];

                    reduxDispatch(loginTypes.SET_APP_VERSION, currentVersion);
                }
            });
        }
    }, [])

    //更版最新版本 (firebase)
    useEffect(() => {
        if (appVersion && newestAppVersion !== appVersion) {
            doSaveAppVersion(newestAppVersion, user);
        }
    }, [appVersion])

    return (<></>)
}

export default withRouter(VerifyLogin);